import { BaseComponentIde } from "../base/componente-ide.js";
import { config } from "../../config.js";
const template = /*html*/`
<div class="w3-row nota">
<div class="w3-col s2 espacio">        
</div>
<div class="w3-col s8">
    <div class="w3-panel w3-pale-blue w3-leftbar w3-border-blue" propiedades>
    <div contenteditable="true">London is the most populous city in the United Kingdom,
    with a metropolitan area of over 9 million inhabitants.</div>
    </div>
</div>
</div>

`;
const style = '';
class Nota extends BaseComponentIde {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;                
    }

    getHtml() {
        const div = this.shadowRoot.querySelector('.nota')    
        let text = `<div class="${div.classList.value}">\n`;
        text += div.innerHTML;        
        text = text.replace(/contenteditable="true"/g, '');
        text += '</div>';
        return text;
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }
}

customElements.define("x-nota", Nota);