function getListaProps(obj){
    let salida = '';
    for(let p in obj.props) {
        salida += `'${p}',`;
    }
    return salida;
}
function getValorVariable(valor) {
    if (typeof valor == 'string') {
        return `'${valor}'`;
    }    
    if (typeof valor == 'object') {
        return JSON.stringify(valor);
    }
    return valor;
}

function getVariables(customScript) {
    let variables = '';
    const data = customScript.data;
    for (let key in data) {
        variables += `        this.${key} = ${getValorVariable(data[key])};\n`;
    }
    return variables;
}

function getFunciones(obj){
    let fun = '';
    const data = obj.metodos;
    for (let f in data) {
        fun += `    ${data[f].toString()}\n`;
    }
    fun += '    ' + obj.init.toString()+'\n';
    return fun;
}

function tagToClassName(tag) {
    let names = tag.split('-');
    let nombre = '';
    for (let name of names) {
        nombre += name[0].toUpperCase();        
        if (name.length > 1) {
            nombre += name.substr(-(name.length-1))
        }
    }
    return nombre;
}
export function getWebcomponent(tag, componentes, customStyle, obj) {
    const nombreClase = tagToClassName(tag);
    let estilo = new Set();
    let style = '';
    let template = '';
    let metodos = '';
    let init = '';
    for(let item of componentes) {
        if (!estilo.has(item.tagName)){
            estilo.add(item.tagName);
            style += item.getStyle();
        }
        template += item.getWebComponenteTemplate();
        metodos += item.getWebComponenteMetodos();
        init += item.getWebComponenteInit();
    }
    template 
    return `
const style = /*css*/\`
.espacio{
    min-height: 10px;
}
${customStyle}
${style}
\`;

const template = /*html*/\`
${template}
\`;

class ${nombreClase} extends HTMLElement {
    constructor(){
        super();
${getVariables(obj)}
        this.render();   
        this.initComponentes();
        this.init();
    }

    initComponentes() {
        ${init}
    }

    ${getFunciones(obj)}

    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        let customTemplate = this.replazaValoresMustache(template);
        shadowRoot.innerHTML = \`
            <link rel="stylesheet" href="https://www.w3schools.com/w3css/4/w3.css">
            <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css">
            <style>\${style}</style>
            <div>\${customTemplate}</div>
        \`;
        this.$el = shadowRoot.querySelector('div');
    }
    /*Cuando se monta en la pagina*/
    connectedCallback(){        
    }
    /*Cuando se desmonta en la pagina, no necesariamente se destruye*/
    disconnectedCallback(){        
    }

    replazaValoresMustache(template){
        let props = [${getListaProps(obj)}];
        let salida = template + '';
        for (let p of props){
            const exp = new RegExp(\`{{\${p}}}\`, 'g');
            const attr = this.getAttribute(p);
            salida = salida.replace(exp, attr);
        }
        return salida;
    }

    ${metodos}

}

customElements.define("${tag}", ${nombreClase});
    `;
}