export const template = /*html*/ `
<div class="w3-row">
<div class="w3-col s12 m12 l12" style="padding-bottom: 8px;">
    <div class="w3-bar w3-light-grey">
        <div class="w3-dropdown-hover">
            <button class="w3-button">Exportar</button>
            <div class="w3-dropdown-content w3-bar-block w3-border">            
                <a href="#" class="w3-bar-item w3-button" @click="exportHtml()">HTML</a>
                <a href="#" class="w3-bar-item w3-button" @click="exportWebComponente()">Web - Componente</a>
                <a href="#" class="w3-bar-item w3-button" @click="exportVueComponente()">Vue</a>
            </div>
        </div>
        <div class="w3-bar-item w3-button w3-right" @click="vistaPrevia()">Vista previa</div>
        <div class="w3-bar-item w3-button w3-right" @click="swapPaneles()">
            <img src="./img/icon/rotate-paneles.svg" style="width: 20px;">
        </div>        
    </div>
</div>
<div class="w3-col s2 m2 l2" id="componentes">
    <div v-for="(grupo, index) in apps" :key="index">
        <button 
            @click="show('grupo'+index)"
            class="w3-padding-16 w3-button w3-block w3-left-align ide-item">{{grupo.nombre}} <i class="fa fa-caret-down"></i></button>
        <div :id="'grupo'+index" class="w3-card w3-hide">
            <a v-for="(app, indexApp) in grupo.apps" :key="indexApp" 
                class="w3-button w3-block w3-left-align"                
                draggable="true"
                @dragstart="dragStar($event,app)"
                href="javascript:void(0)" :app="app.tag">{{app.nombre}}</a>        
        </div> 
    </div>       
</div>
<div class="w3-col s8 m8 l8" 
    id="content-page"
    @drop="drop($event)"
    @dragover="allowDrop($event)"
    style="height: 100vh; background-image: url(./img/cuadro.png); background-repeat: round; max-height: 89vh; overflow: auto;" >
  <div id="panel"
  @dragover="allowDropItem($event)"
  draggable="true"
  contenedor = "true"
  @dragstart="dragStarSwap($event)"
  class="w3-block w3-white w3-border w3-hover-border-blue" style="min-height: 100px;">
    <div class="w3-right basura"><i class="fa fa-trash" id="eliminar"></i></div>
    <div style="top: 0; position: relative; width: fit-content;" class="w3-teal w3-hover-blue w3-tiny">panel</div>
    <p>Arrastra los componetes para empezar</p>
    <p class="w3-small">Nota: Este componente no se tomara encuenta</p>
  </div>  
  
</div>
<div class="w3-col s2 m2 l2" id="propiedades">
</div>
</div>
`;