import  { config } from "../../config.js";
import { Grupo, Vista } from "../../editor-flujos/itemsCanvas.js";
import { addWindowConParametros } from "../../utils/ventana/agregarApps.js";

const template = /*html*/`
<div class="w3-bar w3-black">
<a href="#" class="w3-bar-item w3-button">Agregar</a>
</div>
<x-paginador-files titulo="Grupos" vista-previa="x-vista-previa"></x-paginador-files>
`;

const style = /*css*/`
`;

class PaginadorFlujos extends HTMLElement {
    constructor() {
        super();
        let grupo = new Grupo('Admin', 50, 50);
        const vista = new Vista('Cholo', 200,200);
        grupo.vistaInicial = vista;
        this.lista = [{
            id: 1,
            nombre: 'archivo 1',
            lista: [grupo , vista]
        }, {
            id: 2,
            nombre: 'archivo 2',
            lista: []
        }]
        this.render();
    }

    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `
        <link rel="stylesheet" href="${config.w3css}">
        <style>${style}</style>
        ${template}
        `;
        this.paginador = shadowRoot.querySelector('x-paginador-files');
        this.paginador.setLista(this.lista);
        this.paginador.addEventListener('onVistaPrevia', (e) => {
            const r = e.detail;
            r.vistaPrevia.setLista(r.item.lista);
        });
        this.paginador.addEventListener('onOpenItem', (e) => {
            const r = e.detail;
            let app = addWindowConParametros('x-editor-flujos', r.item.nombre);
            app.setListaObjectos(r.item.lista);
            app.actualiza();
            app.draw();
        });
    }
}

customElements.define('x-paginador-flujos', PaginadorFlujos);