const style = `
.no-activo{
    display: none;
}
`;
class XIframe extends HTMLElement {
    constructor(){
        super();
        this.render();
        this.addListeners();
        this.actualizaActivo('ui');
    } 
  
    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `
        <style>${style}</style>
        <div id="panel" style="height: 100vh; float: left;">
            <x-panel-ide></x-panel-ide>        
        </div>
        <div id="resto" style="float: left;">
            <div class="no-activo" nombre="ui">
                <app-ide></app-ide>
            </div>
            <div nombre="js" style="height: 100%;">
                <x-codigo tema="material"></x-codigo>
            </div>
            <div nombre="css" style="height: 100%;">
                <x-codigo modo="css" tema="material"></x-codigo>
            </div>
        </div>
        `;
        this.padre = this.parentNode;
        this.panel = shadowRoot.querySelector('#panel');
        this.resto = shadowRoot.querySelector('#resto');
        this.setSizesChangePanel(240);
        this.editorJs = this.resto.querySelector('[nombre="js"] x-codigo');
        this.editorCss = this.resto.querySelector('[nombre="css"] x-codigo');
        this.editorUi = this.resto.querySelector('[nombre="ui"] app-ide');
    }

    actualizaActivo(activo) {
        for(let div of this.resto.children) {
            div.classList.value = 'no-activo';
        }
        let div = this.resto.querySelector(`[nombre="${activo}"]`);
        div.classList.value = '';
        this.actualizaEditor(div);
    }

    actualizaEditor(div) {
        let editor = div.querySelector('x-codigo');
        editor ? editor.actualiza() : '';
    }

    actualizaEditores(){
        let js = this.resto.querySelector(`[nombre="js"]`);
        let css = this.resto.querySelector(`[nombre="css"]`);
        this.actualizaEditor(css);
        this.actualizaEditor(js);
    }

    addListeners() {
        let xpanel = this.panel.querySelector('x-panel-ide');
        xpanel.addEventListener('changeSize', (ev)=>{
            const re = ev.detail;
            this.setSizesChangePanel(re.ancho);
            setTimeout(()=>{
                xpanel.click();
            }, 300)
            
        });
        xpanel.addEventListener('activo', (ev) => {
            const activo = ev.detail.activo;
            this.actualizaActivo(activo);
        });
        this.editorCss.addEventListener('changeCode', data=>{
            this.editorUi.setCustomStyle(data.detail.code);
        });
        this.editorJs.addEventListener('changeCode', data=>{
            this.editorUi.setCustomScript(data.detail.code);
        });
        this.actualizaEditores();
    }

    setSizesChangePanel(anchopanel){
        const ancho = this.padre.clientWidth;
        const alto = this.padre.clientHeight;
        this.resto.style.width  = (ancho - anchopanel - 70) + 'px';
        this.panel.style.height = (alto - 60) + 'px';
        this.resto.style.height = (alto - 60) + 'px'; 
    }

    setSizes() {
        const ancho = this.padre.clientWidth;
        const alto = this.padre.clientHeight;
        const anchopanel = this.panel.clientWidth;
        this.panel.style.height = (alto - 60) + 'px';
        this.resto.style.height = (alto - 60) + 'px';        
        this.resto.style.width  = (ancho - anchopanel - 70) + 'px';        
    }

    connectedCallback() {
        const padre = this.parentNode;        
        const config = { attributes: true};
        const that = this;
        var observer = new MutationObserver(function(mutations) {
            that.setSizes();
        });
        observer.observe(padre, config);
    }
}

customElements.define("x-ide", XIframe); //Register the new element