import {config} from '../../config.js';
import { ModalGet } from '../../utils/modal/modal-get.js';
import { BaseComponentIde } from '../base/componente-ide.js';
const style = /*css*/`
.articulo-autor{
    font-style: italic;    
}

.articulo-autor-content{    
    font-size: xx-small;
}
.articulo-titulo {
    font-family: fantasy;
}
.articulo-img{
    width: 100%;
    border-radius: 5px;
}
`;

const template = /*html*/
`
<div class="w3-row articulo">
    <div class="w3-col s2 espacio">        
    </div>
    <div class="w3-col s8">
        <h1 contenteditable="true" class="articulo-titulo" propiedades>Lorem ipsum dolor sit amet</h1>
        <p  class="articulo-autor-content">
        Por <span class="articulo-autor" contenteditable="true" propiedades>Juan Prez Garca</span>
        </p>
        <hr>
        <p contenteditable="true" propiedades>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. 
        Eos accusamus eius et totam obcaecati? Reprehenderit, facilis,
        voluptate nulla praesentium sequi delectus animi corrupti itaque ad ducimus,
         totam suscipit pariatur quos!
         Lorem ipsum dolor sit amet consectetur adipisicing elit. 
         Vitae fugiat magnam, exercitationem, qui quas est rerum 
         molestiae, quis mollitia tenetur consequuntur? 
         Numquam laboriosam facere expedita temporibus quos sint 
         id doloremque?
        </p>
        <img src="./img/libres/lago.jpg" class="articulo-img">
        <p contenteditable="true" propiedades>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. 
        Eos accusamus eius et totam obcaecati? Reprehenderit, facilis,
        voluptate nulla praesentium sequi delectus animi corrupti itaque ad ducimus,
         totam suscipit pariatur quos!
         Lorem ipsum dolor sit amet consectetur adipisicing elit. 
         Vitae fugiat magnam, exercitationem, qui quas est rerum 
         molestiae, quis mollitia tenetur consequuntur? 
         Numquam laboriosam facere expedita temporibus quos sint 
         id doloremque?
        </p>
    </div>
    <div class="w3-col s2">
    </div>
</div>
`;

class Articulo extends BaseComponentIde {
    constructor(){
        super();        
        this.render();
    }
  
    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;            
    }

    onChangeImg(){
        const img = this.shadowRoot.querySelector('img');
        let dlg = new ModalGet('Dame url de imagen', img.src);
        dlg.open().then(dlg => {
            img.src = dlg.data;
        });
    }

    connectedCallback(){        
        this.addListeners();
    }

    addListeners(){
        const img = this.shadowRoot.querySelector('img');
        img.addEventListener('click', this.onChangeImg.bind(this));        
    }

    disconnectedCallback(){
        console.log("Unmounted!")
    }

    getHtml() {        
        let text = '<div class="w3-row">\n';
        text += this.shadowRoot.querySelector('.articulo').innerHTML;
        text = text.replace(/contenteditable="true"/g, '');
        text += '</div>';
        return text;
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }
}

customElements.define("x-articulo", Articulo);