import {config} from '../../config.js';
import { styleEditable } from '../../comun-style-editable.js';
let contComponente = 1;

function creaEvento(id, nombreEvento, accion) {
    return `
        const item${id} = this.shadowRoot.querySelector('#${id}');
        item${id}.addEventListener('${nombreEvento}', (ev) =>{
            this.${accion};
        });
    `;
}

function procesaEventos(item){
    let eventos = '';
    const listaAttr = item.attributes;
    for(let attr of listaAttr){
        if (attr.nodeName.indexOf('ev-')>=0){
            const nombre = attr.nodeName.replace('ev-', '');
            eventos += creaEvento(item.id, nombre, attr.value);
        }
    }
    return eventos;
}

function getAllEvents(div) {
    let eventos = '';
    let lista = div.querySelectorAll('[tiene-eventos]');        
    for(let item of lista) {
        eventos += procesaEventos(item);
    }
    return eventos;
}
export class BaseComponentIde extends HTMLElement {
    constructor(){
        super();
        this.propiedades = null;
        this.modelo = null;
        this._id = contComponente++;
    }

    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `
        <link rel="stylesheet" href="${config.w3css}">
        <style>${styleEditable}</style>
        <style id="custom-style"></style>
        `;        
    }

    setCustomStyle(style) {
        let customStyle = this.shadowRoot.querySelector('#custom-style');
        customStyle.innerHTML = style;
    }

    connectedCallback(){        
        //console.log('Item montado');
    }

    disconnectedCallback(){
        //console.log("Unmounted!")
    }

    setModelo(modelo){
        this.modelo = modelo;
    }

    setPropiedades(propiedades) {
        this.propiedades = propiedades;        
        this.addListenersPropiedades();
    }

    addListenersPropiedades() {
        const itemPropiedades = this.shadowRoot.querySelectorAll('[propiedades]');
        for(let p of itemPropiedades) {
            p.addEventListener('click', (e) => {
                this.propiedades.setItem(this.constructor.name, e.target);
            })
        }
    }

    getHtml() {        
        throw 'No esta definido html en '+this.constructor.name;
    }

    getStyle() {        
        throw 'No esta definido style en '+this.constructor.name;
    }

    getScrip() {
        throw 'No esta definido js en '+this.constructor.name;
    }
    /**Remueve los elementos de no-usar */
    removerNoUsar(div){
        let noUsar = div.querySelectorAll('[no-usar]');
        for(let item of noUsar){
            item.remove();
        }
    }

    getWebComponenteTemplate() {
        console.log('No esta definido getWebComponenteTemplate en '+this.constructor.name);
        return this.getHtml();
    }

    getWebComponenteMetodos() {
        console.log('No esta definido getWebComponenteMetodos en '+this.constructor.name);
        return '';
    }

    getWebComponenteInit() {
        console.log('No esta definido getWebComponenteInit en '+this.constructor.name);        
        return getAllEvents(this.shadowRoot);
    }
}