import { config } from '../../vanilla/config.js';
import {style} from './style.js';
import { cretateVueApp } from './ide.vue.js';


class Ide extends HTMLElement {
    constructor(){
        super();
        this.render();
    } 
  
    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `
            <link rel="stylesheet" href="${config.w3css}">
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            <div></div>
        `;        
        const div = shadowRoot.querySelector('div');
        const desk = document.querySelector('.escritorio');
        this.app = cretateVueApp(div);     
    }

    setCustomStyle(style) {        
        this.app.setCustomtyle(style);        
        let componentes = this.shadowRoot.querySelectorAll('[componente]');
        for (let item of componentes) {            
            item.setCustomStyle(style);
        }
    }

    setCustomScript(codigo) {        
        this.app.setCustomScript(codigo);
    }
}

customElements.define("app-ide", Ide); //Register the new element