import { BaseComponentIde } from "../../base/componente-ide.js";
import { config } from "../../../config.js";
import { ModalForm } from "../../../utils/modal/modal-form.js";
const template = /*html*/`
<div class="w3-bar w3-red bar-dropdown">
    <a href="#" class="w3-bar-item w3-button w3-xlarge" contenteditable="true">Home</a>
    <div class="w3-dropdown-hover w3-right">
    <button class="w3-button w3-xlarge" >&#9776;</button>
    <div class="w3-dropdown-content w3-bar-block w3-border " >
      <span class="w3-bar-item w3-button" id="btn-agregar" no-usar>+</span>       
    </div>
  </div>
</div>
`;
const style = '';
let dlg = new ModalForm('Link');
dlg.innerHtml = `
<div class="w3-container">
    <label>Nombre</label>
    <input class="w3-input" type="text" name="nombre" dato-salida>
    <label>Ruta</label>
    <input class="w3-input" type="text" name="ruta" dato-salida>
</div>
`;
class BarDropDown extends BaseComponentIde {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;
        this.addListers();
    }

    addListers() {
        const agregar = this.shadowRoot.querySelector('#btn-agregar');
        const contenedor = this.shadowRoot.querySelector('.w3-dropdown-content');
        agregar.addEventListener('click', e=> {
            let span = document.createElement('a');
            span.classList.value = 'w3-bar-item w3-button';
            span.setAttribute('link', '');
            span.setAttribute('ruta', '#');
            span.innerHTML = 'Link';
            contenedor.appendChild(span);
            span.addEventListener('click', e => {
                this.editLink(span);
                e.stopPropagation();
                e.preventDefault();
            })
            
        });
    }

    editLink(link){
        dlg.setDefaultValue(
            {
                ruta: link.getAttribute('ruta'),
                nombre: link.innerHTML
            }
        )
        dlg.open().then(r => {
            link.setAttribute('ruta', r.data.ruta);
            link.innerHTML = r.data.nombre;
        });
    }
        
    getHtml() {    
        const menu = this.shadowRoot.querySelector('.w3-dropdown-content');
        menu.style.cssText = 'right: 0;';
        const div = this.shadowRoot.querySelector('.bar-dropdown');
        let text = `<div class="${div.classList.value}">\n`;
        text += div.innerHTML;
        text = text.replace(/contenteditable="true"/g, '');
        text += '</div>';
        menu.style.cssText = '';
        return text;
    }

    getStyle() {
        return style;
    }

    getScrip() {
        '';
    }
}

customElements.define("x-bar-dropdown", BarDropDown);