
import '../info/win.info.js';
import { Bloquear } from '../../vanilla/utils/modal/bloquear.js';
import { addWindow } from '../../vanilla/utils/ventana/agregarApps.js';
const template = /*html*/`
<div class="w3-dropdown-hover w3-left">    
    <button class="w3-button w3-black">Apps</button>
    <div class="w3-dropdown-content w3-card-4 menu-inicio" style="z-index: 9999;">
        <input class="w3-input" type="text" style="width:100%" placeholder="Buscar" @input="onbuscar($event)" v-model="buscar">
        <div class="w3-row">
        <div class="w3-col m6">
            <!--<button class="w3-button">
            <img style="width: 20px;" src="https://openclipart.org/download/26952/Architetto----Formulario.svg">
            Vistas
        </button>-->
            <button class="w3-button" @click ="setActivo('utilidades')">
            <img style="width: 20px;" src="https://openclipart.org/download/280995/RoughPipeWrench.svg">
            Utilidades
            </button>
            <button class="w3-button" @click ="setActivo('configuraciones')">
            <img style="width: 20px;" src="https://openclipart.org/download/169903/1336392323.svg">
            Configuraciones
            </button>
            <button class="w3-button" @click ="setActivo('vistas')">
            <img style="width: 20px;" src="./img/editor-flujos/form.png">
            Vistas
            </button>
            <!--<button class="w3-button">
            <img style="width: 20px;" src="https://openclipart.org/download/183014/1378371919.svg">
            Proyecto
        </button>-->
        </div>
        <div class="w3-col m6">
            <div class="w3-button" v-for="(item,index) in apps" @click="abrir(item.componente, item.nombre, item.resize, item.maximizar)" :key='index'>          
            {{item.nombre}}
            </div>
        </div>
        </div>
        
        <button class="w3-button salir" @click="bloquear()">
        <img style="width: 20px;" src="https://openclipart.org/download/197891/mono-logout.svg">
        Bloquear
        </button>
    </div>    
</div> 
`;

const apps = {
    utilidades: [{
        componente: 'x-calculadora',
        nombre: 'Calculadora',
        resize: 0
    },
    {
        componente: 'win-info',
        nombre: 'Sobre..'
    }],
    configuraciones: [{
        componente: 'config-desk',
        nombre: 'Escritorio'
    }],
    vistas: [{
        componente: 'app-ide',
        nombre: 'Editor vistas',
        maximizar: true
    }]
}

const desk = document.querySelector('.escritorio');

let app = new Vue({
    el: '#menu-inicio',
    template: template,
    data: ()=>{
        return {
            apps: [],
            buscar: ''
        }
    },
    mounted() {
    },
    methods: {
        setActivo(e) {
            this.apps = apps[e];            
            this.buscar = '';
        },
        abrir(componente, titulo, resize, maximizar){
            addWindow(componente, titulo, resize, maximizar);
        },
        onbuscar(val) {
            let cad = val.target.value;
            if (cad == '') {
                return;
            }
            let keys = Object.keys(apps);
            this.apps = [];
            for(let key of keys){
                for(let app of apps[key]){
                    if (app.nombre.indexOf(cad)>=0) {
                        this.apps.push(app);
                    }
                }
            }
        },
        bloquear() {
            this.bloquea(false, 'admin');
        },
        bloquea(hasError, mensaje ) {
            let m = new Bloquear();
            m.open(hasError, mensaje).then(r => {
                if (r.data.password != 'admin'){
                    this.bloquea(true, 'Es admin');
                }
            })
        }
    }
});