import { config } from '../config.js';

const template = /*html*/`
`;

const variablesStyle = /*css*/`
.variables{
    --tamanio: 240px;
    --visible-titulo: '';
    --positionx-central: calc( var(--tamanio) - 20px);
}
`;

const style = /*css*/`
.panel {
    height: 100%;
    width: var(--tamanio);
    background: #132637;
    color: #99a9b8;
    /*transition: width 0.2s;*/
}
.espacio{
    height: 10px;
    width: 100%;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%;
    /*background-color: #f1f1f1;
    border: 1px solid #555;*/
}

li {
    display: block;    
    padding: 8px 16px;
    text-decoration: none;
}
li span{
    padding-left: 10px;
}
li:hover {
    background-color: #163048;
    color: white;
}
.activo {    
    color: white;
    background-color: #163048;
}
.titulo{
    display: var(--visible-titulo);
}
.central{
    color: #99a9b8;
    margin: 0;
    position: absolute;
    top: 45%;    
    transform: translateX(var(--positionx-central));
    background: #132637;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    /*transition: transform 0.2s;*/
    z-index: 10;
}
.icono{
    font-size: 40px;
}
.icono:hover {
    color: white;
}
`;

class PanelIde extends HTMLElement {
    constructor() {
        super();
        this.render();
        this.addListeners();        
    }

    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `        
        <link rel="stylesheet" href="${config.iconicss}"> 
        <style id="config">${variablesStyle}</style>
        <style>${style}</style>
        
       <div class="variables panel">
        <div class="espacio"></div>        
        <ul>
        <li nombre="ui"><i class="icss-html5"></i><span class="titulo">Ui</span></li>
        <li nombre="js"><i class="icss-js"></i><span class="titulo">Javascript</span></li>
        <li nombre="css"><i class="icss-css3"></i><span class="titulo">CSS</span></li>       
        </ul>               
       </div>
       <div class="variables central">
       <i id="flecha" class="icss-caret-l icono"></i>
        </div>
        `;
    }

    addListeners() {
        let central = this.shadowRoot.querySelector('.central');        
        central.addEventListener('click', ev => {
            this.toogleSize();
        });
        let listaLi = this.shadowRoot.querySelectorAll('li');
        for(let li of listaLi) {
            li.addEventListener('click', ev => {
                this.removerActivo(listaLi);
                li.classList.add('activo');
                this.emiteSelectItem(li.getAttribute('nombre'));
                ev.preventDefault();
            });
        }
    }

    removerActivo(lista) {
        for(let li of lista){
            li.classList.remove('activo');
        }
    }

    emiteSelectItem(activo) {
        let event = new CustomEvent("activo", {
            detail: {
                activo: activo
            }
        });
        this.dispatchEvent(event);
    }

    emiteChangeSize(ancho){
        let event = new CustomEvent("changeSize", {
            detail: {
                changeSize: true,
                ancho: ancho
            }
        });

        this.dispatchEvent(event);
    }

    toogleSize() {
        let style = this.shadowRoot.querySelector('#config');
        let flecha = this.shadowRoot.querySelector('#flecha');
        let tamanio = '50px';
        let display = 'none';
        flecha.classList.value = 'icss-caret-r icono';
        if (style.innerHTML.indexOf('--tamanio: 50px;') >= 0) {
            tamanio = '240px';
            display = "''";
            flecha.classList.value = 'icss-caret-l icono';
            this.emiteChangeSize(240);
            
        }
        style.innerHTML = `
    .variables{
        --tamanio: ${tamanio};
        --visible-titulo: ${display};
        --positionx-central: calc( var(--tamanio) - 20px);
    }
        `;
        this.emiteChangeSize(50);
    }
}

customElements.define('x-panel-ide', PanelIde);