export function addWindow(componente, titulo, resize, maximizar) {
    const desk = document.querySelector('.escritorio');
    const win = document.createElement('x-window');
    const app = document.createElement(componente);
    win.appendChild(app);
    win.setAttribute('titulo', titulo);    
    desk.appendChild(win);
    win.setAttribute('activo', 1);
    if (maximizar) {
        win.setAttribute('maximizar', 'true');
    }
    win.setAttribute('size', JSON.stringify({ancho: 300, alto: 400}));
    if(resize == 0){
        win.setAttribute('resize', resize);
    }
    return app;
}

export function addWindowConParametros(componente, titulo, parametros,resize) {
    let app = addWindow(componente, titulo, resize);
    return app;
}