function getValorVariable(valor) {
    if (typeof valor == 'string') {
        return `'${valor}'`;
    }    
    if (typeof valor == 'object') {
        return JSON.stringify(valor);
    }
    return valor;
}

function getVariables(customScript) {
    let variables = '//variables\n';
    const data = customScript.data;
    if (!data) {return '';}
    for (let key in data) {
        variables += `    let ${key} = ${getValorVariable(data[key])};\n`;
    }
    return variables;
}

function getFunciones(obj){
    let fun = '//funciones\n';
    const data = obj.metodos;
    if (!data) {return '';}
    for (let f in data) {
        fun += `    function ${data[f].toString().replace(/this./g, '')}\n`;
    }
    fun += '    function ' + obj.init.toString().replace(/this./g, '')+'\n';
    return fun;
}

export function getHtml(componentes, customstyle, customScript) {
    let estilo = new Set();
    let text = `<html>
    <title>W3.CSS</title>
    <meta name="viewport" content="width=device-width, initial-scale=1">
    <link rel="stylesheet" href="https://www.w3schools.com/w3css/4/w3.css">
    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css">
    <style>
    ${customstyle}
    .espacio{
        min-height: 10px;
    }`;
    for(let item of componentes) {
        if (!estilo.has(item.tagName)){
            estilo.add(item.tagName);
            text += item.getStyle();
        }                        
    }
    text += '</style><body>';
    for(let item of componentes) {
        text += item.getHtml();
    }
    text += '<script>\n';
    text += getVariables(customScript);
    text += getFunciones(customScript);
    for(let item of componentes) {
        const strScript = item.getScrip();        
        text += strScript;
    }
    text += 'init();';
    text+=`\n</script>\n</body>\n</html>`;
    text = text.replace(/ev-/g,'on');
    return text;

}