import { BaseComponentIde } from "../base/componente-ide.js";
import { ModalGet } from "../../utils/modal/modal-get.js";
const template = /*html*/`
<div class="w3-row imagen">
<div class="w3-col s2 espacio">        
</div>
<div class="w3-col s8">
    <img src="./img/libres/lago.jpg" class="articulo-img">
</div>
</div>

`;
const style = `
.articulo-img{
    width: 100%;
    border-radius: 5px;
}
`;
class TextoImagen extends BaseComponentIde {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <style>${style}</style>
            ${template}
        `;
        this.addListeners();            
    }

    addListeners(){
        const img = this.shadowRoot.querySelector('img');
        img.addEventListener('click', this.onChangeImg.bind(this));
    }

    onChangeImg(){
        const img = this.shadowRoot.querySelector('img');
        let dlg = new ModalGet('Dame url de imagen', img.src);
        dlg.open().then(dlg => {
            img.src = dlg.data;
        });
    }

    getHtml() {
        const div = this.shadowRoot.querySelector('.imagen')    
        let text = `<div class="${div.classList.value}">\n`;
        text += div.innerHTML;        
        text = text.replace(/contenteditable="true"/g, '');
        text += '</div>';
        return text;
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }
}

customElements.define("x-imagen", TextoImagen);