import { BaseComponentIde } from "../base/componente-ide.js";
import { ModalGet } from "../../utils/modal/modal-get.js";
const template = /*html*/`
<div class="w3-display-container titulo-imagen">
  <img src="./img/libres/lago.jpg"  style="width:100%;">
  <div class="w3-display-middle w3-large">
    <h1 class="w3-jumbo w3-text-white w3-wide" propiedades contenteditable="true" style="font-weight: bold;">DEADLINE</h1>
  </div>
   <div class="w3-display-bottomright w3-container">
      <p class="w3-text-white w3-xlarge" propiedades contenteditable="true">Venezia street 16-17 August</p>
   </div>
</div>
`;
const style = '';
class TituloImagen extends BaseComponentIde {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <style>${style}</style>
            ${template}
        `;
        this.addListeners();
    }

    onChangeImg(){
        const img = this.shadowRoot.querySelector('img');
        let dlg = new ModalGet('Dame url de imagen', img.src);
        dlg.open().then(dlg => {
            img.src = dlg.data;
        });
    }

    addListeners(){
        const img = this.shadowRoot.querySelector('img');
        img.addEventListener('click', this.onChangeImg.bind(this));        
    }

    getHtml() {
        const div = this.shadowRoot.querySelector('.titulo-imagen')    
        let text = `<div class="${div.classList.value}">\n`;
        text += div.innerHTML;        
        text = text.replace(/contenteditable="true"/g, '');
        text += '</div>';
        return text;
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }
}

customElements.define("x-titulo-imagen", TituloImagen);