import { BaseComponentIde } from "../base/componente-ide.js";
import { config } from "../../config.js";
const template = /*html*/`
<div class="w3-row comentario">
<div class="w3-col s2 espacio">        
</div>
<div class="w3-col s8">
    <blockquote class="w3-leftbar w3-pale-green w3-border-red w3-padding" contenteditable="true" propiedades>
        <p><i class="fa fa-quote-right w3-xxlarge"></i></p>
        <p>Make it as simple as possible, but not simpler.</p>
        <p style="font-weight: bold;" class="w3-small w3-bold">Albert Einstein</p>
    </blockquote>
</div>
</div>

`;
const style = '';
class TextoComentario extends BaseComponentIde {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <link rel="stylesheet" href="${config.host}/fonts/fafafont/font-awesome.min.css">
            <style>${style}</style>
            ${template}
        `;                
    }

    getHtml() {
        const div = this.shadowRoot.querySelector('.comentario')    
        let text = `<div class="${div.classList.value}">\n`;
        text += div.innerHTML;        
        text = text.replace(/contenteditable="true"/g, '');
        text += '</div>';
        return text;
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }
}

customElements.define("x-comentario", TextoComentario);