

import { BaseComponentIde } from "../../base/componente-ide.js";
import { config } from "../../../config.js";
import { templateHtml, templateJs } from "./templatesHtml.js";
import { ModalForm } from "../../../utils/modal/modal-form.js";
const template = /*html*/`
<div class="bar w3-card">
    <div class="w3-bar w3-red">
        <span href="#" class="w3-bar-item w3-button w3-xlarge" contenteditable="true" id="titulo" propiedades>Ttulo</span>
        <button class="w3-bar-item w3-button w3-red w3-xlarge w3-right" id="btn-menu">&#9776;</button>
    </div>
    <div class="w3-sidebar w3-bar-block" style="display:none;" id="right-menu">
        <span class="w3-bar-item w3-button w3-large ancho-menu" id="btn-cerrar" no-usar contenteditable="true">Close &times;</span>
        <span class="w3-bar-item w3-button w3-large ancho-menu" id="btn-agregar" no-usar>+</span>        
    </div>
</div>
`;
const style = `.ancho-menu{
    width: auto !important;
}`;

let dlg = new ModalForm('Link');
dlg.innerHtml = `
<div class="w3-container">
    <label>Nombre</label>
    <input class="w3-input" type="text" name="nombre" dato-salida>
    <label>Ruta</label>
    <input class="w3-input" type="text" name="ruta" dato-salida>
</div>
`;

class Bar extends BaseComponentIde {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;
        this.addListeners();              
    }

    getLinks() {
        const links = this.shadowRoot.querySelectorAll('[link]');
        let linksOut = [];
        for(let link of links) {
            linksOut.push({
                ruta: link.getAttribute('ruta'),
                nombre: link.innerHTML
            });
        }
        return linksOut;
    }

    getHtml() {
        const titulo = this.shadowRoot.querySelector('#titulo').innerText;
        const cerrar = this.shadowRoot.querySelector('#btn-cerrar').innerText;
        const text = templateHtml(titulo, cerrar, this.getLinks(), this._id);
        return text;
    }

    addListeners() {
        const menu = this.shadowRoot.querySelector('#btn-menu');
        const cerrar = this.shadowRoot.querySelector('#btn-cerrar');
        menu.addEventListener('click', (e) => {
            let display = this.shadowRoot.getElementById("right-menu").style.display;
            display == 'contents' ? display = 'none' : display = 'contents'

            this.shadowRoot.getElementById("right-menu").style.display = display;
        });        
        cerrar.addEventListener('click', e=> {
            //this.shadowRoot.getElementById("right-menu").style.display = "none";
        });
        const agregar = this.shadowRoot.querySelector('#btn-agregar');
        const contenedor = this.shadowRoot.querySelector('.w3-sidebar');
        agregar.addEventListener('click', e=> {
            let span = document.createElement('a');
            span.classList.value = 'w3-bar-item w3-button';
            span.setAttribute('link', '');
            span.setAttribute('ruta', '#');
            span.innerHTML = 'Link';
            contenedor.appendChild(span);
            span.addEventListener('click', e => {
                this.editLink(span);
            })
            
        });
        const links = this.shadowRoot.querySelectorAll('[link]');
        for(const link of links){
            link.addEventListener('click', (e) => {
                e.stopPropagation();
                this.editLink(link);
            });
        }
    }

    editLink(link){
        dlg.setDefaultValue(
            {
                ruta: link.getAttribute('ruta'),
                nombre: link.innerHTML
            }
        )
        dlg.open().then(r => {
            link.setAttribute('ruta', r.data.ruta);
            link.innerHTML = r.data.nombre;
        });
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return templateJs(this._id);
    }

    getWebComponenteTemplate() {
        const original = this.shadowRoot.querySelector('.bar');
        const div = original.cloneNode(true);
        this.removerNoUsar(div);
        let text = `<div class="${div.classList.value}">\n`;
        text += div.innerHTML;        
        text = text.replace(/contenteditable="true"/g, '');
        text += '</div>';
        return text;
    }

    getWebComponenteMetodos() {
        return `
    onMenu${this._id}(){
        let menu = this.shadowRoot.getElementById("right-menu");
        let display = menu.style.display;
        display == 'contents' ? display = 'none' : display = 'contents';
        menu.style.display = display;
    }
    addListeners${this._id}(){
        let menu = this.shadowRoot.getElementById("btn-menu");
        menu.addEventListener('click', (e) => {
            this.onMenu${this._id}();
        });
    }
        `;
    }

    getWebComponenteInit() {
        return `
        this.addListeners${this._id}();
        `;
    }
}

customElements.define("x-bar", Bar);