export class Fecha extends Date{
    constructor() {
        super();
    }

    toTimeString() {
        let minutos = ('0'+this.getMinutes()).substr(-2);
        let hora = this.getHours();
        hora = hora<=12 ? hora : hora - 12;
        let horas = ('0'+hora).substr(-2);
        let isAm = this.getHours() > 12 ? 'p.m.' : 'a.m.';
        return `${horas}:${minutos} ${isAm}`;

    }
}