const template = `
<h4>Propiedades</h4>
<div id="propiedades"></div>
`;

const templateGrupo = /*html*/`
<label>Nombre</label>
<input name="nombre" disabled>
<label>contexto</label>
<input name="contexto">
<label>Vista inicial</label>
<select id="vistas" name="vistaInicial"></select>
`;

const templateVista = /*html*/`
<label>Nombre</label>
<input name="nombre">
<label>contexto</label>
<input name="contexto">
<div class="lista-vistas">
<span class="vista"> Vista 1</span> <span class="vista">Vista 2</span> <span class="vista">Vista 3</span>
</div>
<label>Agregar vista</label>
<select id="vistas" name="vista"></select>
`;

const style= /*css*/`
input,select{
    width: 100%;
}
.lista-vistas{
    padding: 16px;
}
span.vista {
    margin: 5px;
    border: solid black 1px;
    border-radius: 5px;
    padding: 8px;
    color: white;
    background: tomato;
}
`;

class PropiedadesVista extends HTMLElement {
    constructor(){
        super();        
        this.item = null;
        this.divPropiedades = null;
        this.lista = null;
        this.render();      
    }

    render(){
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `
        <style>${style}</style>
        ${template}
        `;
        this.divPropiedades = shadowRoot.querySelector('#propiedades');        
    }
    /** se debe pasar el array de lista para poder trabajar boien con el. */
    setLista(lista) {
        this.lista = lista;
    }
    
    setItem(item) {
        this.item = item;
        this.showPropiedades();
    }

    addOptions() {
        if (!this.lista){
            return;
        }
        let options = '<option disabled>Selecione uno</option>';
        for(const item of this.lista){
            if (item.constructor.name == 'Vista') {
                options += `<option value="${item.id}">${item.nombre}</option>`;
            }
        }
        const listaVistas = this.divPropiedades.querySelector('#vistas');
        listaVistas.innerHTML = options;
    }

    showPropiedades(){
        if (this.item.constructor.name == 'Grupo')   {
            this.divPropiedades.innerHTML = templateGrupo;
            this.addOptions();
            this.setValoresGrupo();            
        } else if (this.item.constructor.name == 'Vista'){
            this.divPropiedades.innerHTML = templateVista;
            this.addOptions();
            this.setValoresVista();
            this.showListaVistas();
        }        
    }

    emitePintar() {
        this.dispatchEvent(
            new CustomEvent('pintar', {
                detail: {
                    pintar: true
                }
            })
        );
    }

    setInputListener(key) {
        const item = this.divPropiedades.querySelector(`input[name="${key}"]`);
        item.value = this.item[key];        
        item.addEventListener('change', (ev) => {            
            this.item[key] = item.value;
            this.emitePintar();
        });
    }

    showListaVistas() {
        const div = this.divPropiedades.querySelector('.lista-vistas');
        div.innerHTML = '';
        for (let vista of this.item.vistas) {
            let span = document.createElement('span');
            span.classList.value = 'vista';
            span.setAttribute('id', vista.id +'');
            span.innerHTML = vista.nombre;
            span.addEventListener('click', (e) => {
                span.remove();
                this.removeFromVista();
            })
            div.appendChild(span);
        }
    }

    removeFromVista(id) {
        let index = this.item.vistas.findIndex(e => {
            return e.id == id;
        });
        this.item.vistas.splice(index,1);
        this.emitePintar();
    }

    setValoresVista() {
        this.setInputListener('nombre');
        this.setInputListener('contexto');
        const svistas = this.divPropiedades.querySelector('#vistas');
        svistas.value = '';
        svistas.addEventListener('change', (ev) =>{
            const id = ev.target.value;
            if (id == '') {
                return;
            }
            this.lista.forEach(element => {
                if (element.id == id){
                    let esta = this.item.vistas.some( e=> {
                        return e.id == id;
                    });
                    if (!esta && id != this.item.id) {
                        this.item.vistas.push(element);
                        this.showListaVistas();
                        this.emitePintar();
                    }
                    svistas.value = '';
                }
            });
        });
    }

    setValoresGrupo() {
        const nombre = this.divPropiedades.querySelector('input[name="nombre"]');
        this.setInputListener('contexto');
        const vistaInicial = this.divPropiedades.querySelector('#vistas');
        nombre.value = this.item.nombre;
        if (this.item.vistaInicial) {
            vistaInicial.value = this.item.vistaInicial.id;
        } else {
            vistaInicial.value = '';
        }
        vistaInicial.addEventListener('change', (ev) =>{
            const id = ev.target.value;
            this.lista.forEach(element => {
                if (element.id == id){
                    this.item.vistaInicial = element;
                    this.emitePintar();
                }
            });
        });
    }

    connectedCallback() {
    }

    disconnectedCallback() {
    }
}

customElements.define('x-propidades-vista', PropiedadesVista);