function getListaProps(obj){
    let salida = '';
    for(let p in obj.props) {
        salida += `'${p}',`;
    }
    return salida;
}
function getValorVariable(valor) {
    if (typeof valor == 'string') {
        return `'${valor}'`;
    }    
    if (typeof valor == 'object') {
        return JSON.stringify(valor);
    }
    return valor;
}

function getVariables(customScript) {
    let variables = '';
    const data = customScript.data;
    for (let key in data) {
        variables += `        ${key}: ${getValorVariable(data[key])},\n`;
    }
    return variables;
}

function getFunciones(obj){
    let fun = '';
    const data = obj.metodos;
    for (let f in data) {
        fun += `    ${data[f].toString()},\n`;
    }
    fun += '    ' + obj.init.toString()+',\n';
    return fun;
}

function tagToClassName(tag) {
    let names = tag.split('-');
    let nombre = '';
    for (let name of names) {
        nombre += name[0].toUpperCase();        
        if (name.length > 1) {
            nombre += name.substr(-(name.length-1))
        }
    }
    return nombre;
}
export function getVueComponent(componentes, customStyle, obj) {    
    let estilo = new Set();
    let style = '';
    let template = '';
    let metodos = '';
    let init = '';
    for(let item of componentes) {
        if (!estilo.has(item.tagName)){
            estilo.add(item.tagName);
            style += item.getStyle();
        }
        template += item.getWebComponenteTemplate();
        metodos += item.getWebComponenteMetodos() + ',';
        init += item.getWebComponenteInit();
    }
    template 
    return `
<template>
${template}
</template>

<script>
export default {    
    data() {
        return {
${getVariables(obj)}
        }
    }
    props: [${getListaProps(obj)}],
    mounted() {
        ${init}
    },
    methods: {
        ${metodos}
        ${getFunciones(obj)}
    }
}
</script>

<style scoped>
${style}
${customStyle}
</style>
    `;
}