const template = /*html*/ `
<div>
    <h3>Desk interface</h3>    
    <strong>Version</strong>: 1<br>
    <strong>Librerias</strong>: 
        <a href="https://www.w3schools.com/w3css/">W3.css</a> 
        <a href="https://animate.style/">Animate css</a>
        <a href="https://vuejs.org/">Vue</a><br>    
    <strong>Autor</strong>: <a href="http://saulsm.epizy.com/">Sal Salazar Mndez</a><br>
    <p>Iterface web que simula un escriotrio virtual, el proposito de esta es dar una opcin 
    ms al usuario para su aplicacin web.</p>
    <p>Esta interface esta basada en las libreras w3css, animate y vue ademas de utilizas web componentes de javascrip.</p>
</div>
`;

class WinInfo extends HTMLElement {
    constructor(){
        super();
        this.render();
    } 
  
    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `            
            <div></div>
        `;
        const div = shadowRoot.querySelector('div');
        let app = new Vue({
            el: div,
            template: template,
        });
    }
}

customElements.define("win-info", WinInfo); //Register the new element