class XIframe extends HTMLElement {
    constructor(){
        super();
        this.render();
    } 
  
    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `            
        <iframe></iframe>
        `;
        const padre = this.parentNode;
        const iframe = shadowRoot.querySelector('iframe');
        //style="position: absolute; top: 38px; left: 0; right: 0; width: 100%;height: 90%;"
        iframe.style.position = "absolute";
        iframe.style.top = "38px";
        iframe.style.right = "0";
        iframe.style.right = "0";        
    }

    connectedCallback() {
        const padre = this.parentNode;
        const iframe = this.shadowRoot.querySelector('iframe');
        const config = { attributes: true};                 
        var observer = new MutationObserver(function(mutations) {
            iframe.style.width = (padre.clientWidth -5 )+"px";
            iframe.style.height = (padre.clientHeight -45 )+"px";            
        });
        observer.observe(padre, config);
    }
    
    static get observedAttributes() {
        return ['src'];
    }
    attributeChangedCallback(name, oldVal, newVal) {
        this[name] = newVal
    }
    set src(val) {
        
            const iframe = this.shadowRoot.querySelector('iframe');
            iframe.src = val;
        
    }
}

customElements.define("x-iframe", XIframe); //Register the new element