const template = /*html*/`
<table class="w3-small w3-table w3-striped w3-bordered w3-border" id ="datos-clase">
</table>
<br>
<span class="w3-button w3-blue w3-right" id="save" style="display: none">save</span>
`;

let contEvent = Date.now();
class PropiedadesHtml extends HTMLElement {
    constructor() {
        super();
        this.item = null;
        this.nombreComponente = '';        
        this.render();
    }

    render() {
        this.innerHTML = template;
        const bsave = this.querySelector('#save');
        bsave.addEventListener('click', (e)=>{this.save()} );
    }

    setItem(nombreComponente, item) {
        this.item = item;
        this.nombreComponente = nombreComponente;
        this.createTabla();
        const bsave = this.querySelector('#save');
        bsave.style.display = '';
    }

    createTabla() {
        let data = this.querySelector('#datos-clase');
        data.classList.value = '';        
        let filas = `
            <tr>                
                <th style="text-align: center;" colspan="3">
                ${this.nombreComponente} 
                </th>
            </tr>
            <tr>
                <th style="width:30%">Propiedad</th>
                <th style="width: 65%">
                Valor                    
                </th>
                <th>
                <i class="fa fa-plus" id="add-propiedad"></i>
                </th>
            </tr>`;            
            filas +=`<tr><td>clases</td>
            <td contenteditable="true" clases colspan="2">
                ${this.item.classList.value}
            </td></tr>`;        
        filas += this.createFilasPropiedadesEstilos();   
        filas += this.createEventPropiedad();     
        data.innerHTML = filas;      
        data.classList.value = 'w3-table-all w3-tiny';
        this.addListenerPlus(data);
        this.addListenerBasurero(data);
    }
    createFilasPropiedadesEstilos(){
        let styles = this.item.style.cssText.split(';');
        let filas = '';
        for(let estilo of styles) {
            if (estilo) {
                let propiedad = estilo.split(':');
                filas += `<tr propiedad>
                    <td contenteditable="true">${propiedad[0]}</td>
                    <td contenteditable="true">
                        ${propiedad[1]}
                    </td>
                    <td>
                        <i class="fa fa-trash" basurero></i>
                    </td>
                </tr>`;
            }
        }
        return filas;
    }
    createEventPropiedad() {
        let  lista =  this.item.attributes;
        let filas = '';
        for (let attr of lista){
            if (attr.nodeName.indexOf('ev-') < 0){
                continue;
            }
            filas += `<tr propiedad>
                <td contenteditable="true">${attr.nodeName}</td>
                <td contenteditable="true">
                    ${attr.value}
                </td>
                <td>
                    <i class="fa fa-trash" basurero></i>
                </td>
            </tr>`;
        }
        return filas;
    }
    addListenerPlus(data) {        
        let plus = data.querySelector('#add-propiedad');
        plus.addEventListener('click', () => {
            const tbody = data.querySelector('tbody');
            let tr = document.createElement('tr');
            tr.innerHTML += `<tr propiedad>
                    <td contenteditable="true">
                    </td>
                    <td contenteditable="true">                            
                    </td>
                    <td>
                        <i class="fa fa-trash" basurero></i>
                    </td>
                </tr>`;
            tbody.appendChild(tr);
            let basurero = tr.querySelector('[basurero]');
            tr.setAttribute('propiedad', '');
            basurero.addEventListener('click', (e)=>{
                this.eliminaFila(e.target.parentNode.parentNode);
            });                
        });
    }
    addListenerBasurero(data) {
        let basurero = data.querySelectorAll('[basurero]');
        for (let b of basurero){
            b.addEventListener('click', (e)=>{
                this.eliminaFila(e.target.parentNode.parentNode);                
            })
        }
    }

    eliminaFila(tr) {
        const td = tr.querySelectorAll('td');
        let nombre = td[0].innerText;
        if (nombre.indexOf('ev-') >= 0) {
            this.item.removeAttribute(nombre);
        }
        tr.remove();
    }

    connectedCallback() {
    }

    disconnectedCallback() {
    }

    save() {
        let id = null;
        if (this.item) {                           
            let data = this.querySelector('#datos-clase');                
            const clases = data.querySelector('[clases]');
            this.item.classList.value = clases.innerHTML.replace(/<br>/g, '');
            const propiedades = data.querySelectorAll('[propiedad]');
            let estilos = '';
            this.item.removeAttribute('tiene-eventos');
            for (const p of propiedades){
                const td = p.querySelectorAll('td');
                if (td[0].innerText.indexOf('ev-') >=0  ) {
                    this.item.setAttribute(td[0].innerText, td[1].innerText);
                    if (id == null){
                        id = contEvent++;
                    }
                    this.item.id = 'ev'+id;
                    this.item.setAttribute('tiene-eventos','');
                } else {
                    estilos += `${td[0].innerText}: ${td[1].innerText}; `;
                }
            }
            this.item.style.cssText = estilos.trim();
        }
    }
}

customElements.define('x-propiedades', PropiedadesHtml);