function distancia (x,y,x1,y1) {
    return Math.sqrt((x-x1)*(x-x1) + (y-y1)*(y-y1));
}

let propiedades = document.querySelector('x-propidades-vista');

class Item{
    constructor(x,y, ancho, alto, color){
        this.x = x;
        this.y = y;        
        this.alto = alto;
        this.ancho = ancho;
        this.isSelect = false;
        this.color = color;
        this.id = cont++;
        this.configuracion = {
            colorActivoConexion: 'yellow',
            colorConexion: 'orange',
            anchoLineaConexion: 3,            
            colorLetra: 'black',
            letra: '12px cursive'
        }
    }
    /** Se utiliza para pintar el objecto*/
    draw(lienso) {
    }
    /** Cuando da click en el area rectangular  regresa true */
    inArea(x,y) {
        if (this.x <= x && (this.x+this.ancho) >= x ) {
            if (this.y <= y && (this.y+this.alto) >= y ) {
                return true;
            }
        }
        return false;
    }
    /** */
    setConfiguracion(configuracion) {
        Object.assign(this.configuracion, configuracion);
    }    
    /** cuando mueve el mouse, se verifica que este este dentro del area del objecto, 
     * si no esta se borra el borde */
    isOut(x,y){
        if ( (x < this.x-10) || (x> this.x+this.ancho+15)) {
            return true;
        }
        if ( (y < this.y-10) || (y> this.y+this.alto+15)) {
            return true;
        }
        return false;
    }
    /** Se utiliza para redimencionar el objecto */
    incrementoLados(incx, incy){
        this.ancho = this.ancho+incx;
        this.alto = this.alto+incy;
        this.ancho < 0 ?  this.ancho = 10 : '';
        this.alto < 0 ?  this.alto = 10 : '';
    }
}

class ItemImg extends Item {
    constructor(nombre,x,y) {
        super(x,y,50,70, 'black');
        this.nombre = nombre;
        this.img = imgGrupo;
        this.contexto = '';
    }

    draw(lienso) {        
        lienso.fillStyle = this.configuracion.colorLetra;
        lienso.font = this.configuracion.letra;
        lienso.fillText(this.nombre, this.x, this.y+this.alto);
        lienso.drawImage(this.img, this.x, this.y );
        if (this.isSelect){
            lienso.strokeStyle = this.configuracion.colorConexion;
            lienso.strokeRect(this.x-10, this.y-10, this.ancho+20, this.alto+20);            
        }       
    }
    
    incrementoLados(incx, incy){        
    }

    inArea(x,y) {
        if (this.x <= x && (this.x+this.ancho) >= x ) {
            if ( (this.y-25) <= y && (this.y+this.alto) >= y ) {                                
                return true;                
            }
        }
        return false;
    }

    drawConexiones(lienso){        
    }

    toJson() {        
        return {
            nombre: this.nombre,
            contexto: this.contexto,
            id: this.id,
            x: this.x,
            y: this.y,
            tipo: this.constructor.name
        }
    }

    fromJson(json) {
        this.nombre = json.nombre;
        this.contexto = json.contexto;
        this.x = json.x;
        this.y = json.y;
        this.id = json.id;
    }

    actualizaVistas(idVistas, lista) {
        for(let id of idVistas){
            for(let vista of lista) {
                if (vista.id == id) {
                    this.vistas.push(vista);
                    break;
                }
            }
        }
    }
}

export class Grupo extends ItemImg{
    constructor(nombre,x,y) {
        super(nombre,x,y);
        this.img = imgGrupo;
        this.vistaInicial = null;
        this.configuracion.letra = '15px cursive';
    }
    /** dibuja una linea */
    drawConexiones(lienso){
        if (!this.vistaInicial) {
            return;
        }
        lienso.lineWidth = this.configuracion.anchoLineaConexion;
        if (this.isSelect){
            lienso.strokeStyle = this.configuracion.colorActivoConexion;
        } else {
            lienso.strokeStyle = this.configuracion.colorConexion;
        }        
        lienso.beginPath();
        lienso.moveTo(this.x+this.ancho/2,this.y+this.alto/2);
        lienso.lineTo(this.vistaInicial.x+this.ancho/2,
            this.vistaInicial.y+this.alto/2);
        lienso.stroke();
        lienso.lineWidth = 1;
    }
    toJson(){
        let json = super.toJson();
        json.vistaInicial = this.vistaInicial ? 
            this.vistaInicial.id : null;
        return json;
    }
    replaceId(lista){
        for(let item of lista){
            if (item.id == this.vistaInicial) {
                this.vistaInicial = item;
                break;
            }
        }
    }
    
    fromJson(json) {
        super.fromJson(json);
        this.vistaInicial = json.vistaInicial;
    }
}

export class Vista extends ItemImg{
    constructor(nombre,x,y) {
        super(nombre,x,y);
        this.img = imgForm;
        this.vistas = [];
    }
    
    drawConexiones(lienso){        
        lienso.lineWidth = this.configuracion.anchoLineaConexion;
        if (this.isSelect){
            lienso.strokeStyle = this.configuracion.colorActivoConexion;
        } else {
            lienso.strokeStyle = this.configuracion.colorConexion;
        }        
        for(let vista of this.vistas){
            lienso.beginPath();
            lienso.moveTo(this.x+this.ancho/2,this.y+this.alto/2);
            lienso.lineTo(vista.x+this.ancho/2,vista.y+this.alto/2);
            lienso.stroke();
        }        
        lienso.lineWidth = 1;
    }

    toJson(){
        let vistas = [];
        for(let vista of this.vistas){
            vistas.push(vista.id);
        }
        let json = super.toJson();
        json.vistas = vistas;
        return json;
    }

    replaceId(lista){
        let vistas = []
        for (const id of this.vistas){
            for(let item of lista){
                if (item.id == id) {
                    vistas.push(item);
                    break;
                }
            }
        }
        this.vistas = vistas;
    }

    fromJson(json) {
        super.fromJson(json);
        this.vistas = json.vistas;
    }
}

function espera() {
    return new Promise( resolve => {
        setTimeout( () => {
            resolve(true);
        }, Math.random()*1000);
    } );
}

export function listaObjectosToJson(listaObjectos){    
    let lista = [];
    for(let item of listaObjectos){
        lista.push(item.toJson());
    }
    return lista;
}

export function jsonToListaObjectos(json) {
    let lista = [];
    for(let item of json){
        if (item.tipo == 'Grupo'){
            let grupo = new Grupo();
            grupo.fromJson(item);
            lista.push(grupo);
        } else if (item.tipo == 'Vista') {
            let grupo = new Vista();
            grupo.fromJson(item);
            lista.push(grupo);
        }
    }
    for (let item of lista) {
        item.replaceId(lista);
    }
    return lista;
}