import { BaseComponentIde } from "../base/componente-ide.js";
import { w3CodeColor } from "../../utils/colores/colores.js";
const template = /*html*/`
<div class="w3-row codigo">
<div class="w3-col s2 espacio">        
</div>
<div class="w3-col s8">
    <div class="w3-panel w3-card w3-light-grey"> 
    <span class="w3-button w3-blue w3-right" id="actualiza" no-usar>actualiza</span>
    <h4 propiedades>Cdigo js</h4>
    <div></div>
    <div class="w3-code jsHigh notranslate" contenteditable="true" editor>
        let frutas = [];<br>
        frutas[0] = "Platano";<br>
        frutas[1] = "Manzana";<br>
        frutas[2] = "Mango";<br>
        frutas[3] = "Naranja";<br>
    </div>
    </div>    
</div>
</div>

`;
const style = '';
class TextoCodigo extends BaseComponentIde {
    constructor() {
        super();
        this.render();        
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <style>${style}</style>
            ${template}
        `;
        let boton = shadowRoot.querySelector('#actualiza');
        boton.addEventListener('click', (ev) => {
            this.actulizaCodigo();
        });
        w3CodeColor(shadowRoot);
    }

    actulizaCodigo() {
        let editor = this.shadowRoot.querySelector('[editor]');
        let texto = editor.innerText.split('\n');
        editor.innerHTML = '';
        for(let linea of texto){
            editor.innerHTML += linea + '<br>';
        }
        w3CodeColor(this.shadowRoot);
    }

    getHtml() {
        const original = this.shadowRoot.querySelector('.codigo');
        const div = original.cloneNode(true);
        this.removerNoUsar(div);
        let text = `<div class="${div.classList.value}">\n`;
        text += div.innerHTML;        
        text = text.replace(/contenteditable="true"/g, '');
        text += '</div>';
        return text;
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }
}

customElements.define("x-codigo-js", TextoCodigo);