import { BaseComponentIde } from "../base/componente-ide.js";
const template = /*html*/`
<div class="w3-container w3-red w3-center titulo-jumbo" contenteditable="true" propiedades>
    <h1 class="w3-margin w3-jumbo">Pgina de inicio</h1>
    <p class="w3-xlarge">Usando w3.css</p>
</div>
`;
const style = '';
class TituloJumbo extends BaseComponentIde {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <style>${style}</style>
            ${template}
        `;              
    }

    getHtml() {    
        const div = this.shadowRoot.querySelector('.titulo-jumbo')    
        let text = `<div class="${div.classList.value}">\n`;
        text += div.innerHTML;
        text = text.replace(/contenteditable="true"/g, '');
        text += '</div>';
        return text;
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }
}

customElements.define("x-titulo-jumbo", TituloJumbo);