import './appVue/menu-escritorio/menu-escritorio.js';
import './appVue/calculadora/calculadora.js';
import './appVue/ide/propiedades/propiedades.js';
import './appVue/ide/ide.js';
import './appVue/config-desk/config-desk.js';
import './appVue/flujos/paginador/paginador-flujo.js';
import './appVue/flujos/paginador/formulario-flujo.js';
import './vanilla/vanilla.componentes.js';
import { drop, allowDrop } from './vanilla/dragdrop.js';


const desk = document.querySelector('.escritorio');
desk.addEventListener('drop', (e) => {drop(e);});
desk.addEventListener('dragover', (e) => {allowDrop(e);});


desk.style.backgroundImage = `url("./img/gibli.jpg")`;
desk.style.backgroundRepeat = 'round';

