import { config } from "../../../vanilla/config.js";
import { addWindowConParametros } from "../../../vanilla/utils/ventana/agregarApps.js";

const style = /*css*/`
`;
const template = /*html*/ `
<div>    
<h1 class="w3-xxlarge w3-text-blue">Flujo</h1>        
    <div class="w3-row">
        <div class="w3-col s12 m12 l12" >            
            <p v-if="item">Flujo de {{item.nombre}}</p>                        
        </div>
        <div class="w3-col s12 m12 l12">
        <div id="grafica" style="height: 400px;"></div>
        </div>        
    </div>    
</div>
`;

function addDiagrama(el, item) {
    let nodes = [
        {id: 1, label: 'Admistrador', shape: 'box', color:'#97C2FC', data: {
          x() {
            console.log('eco');
          }
        }},
        {id: 2, label: 'Login', shape: 'circle', color:'#FFFF00'},
        {id: 3, label: 'Menu\nPrincipal', shape: 'diamond', color:'#FB7E81'},
        {id: 9, label: 'Menu\nSecundario 1', shape: 'diamond', color:'#FB7E81'},
        {id: 10, label: 'Menu\nSecundario 2', shape: 'diamond', color:'#FB7E81'},    
        {id: 4, label: 'Paginador\ncolores', shape: 'circle', color:'blue', font: {color:'white'}},
        {id: 5, label: 'Editar\ncolores', shape: 'circle', color:'green', font: {color:'white'}},
        {id: 6, label: 'Nuevo\ncolores', shape: 'circle', color:'green', font: {color:'white'}},
    ];
    
    // create an array with edges
    let edges = [
        {from: 1, to: 2, color:{color:'red', highlight:'blue'}},
        {from: 2, to: 3, color:'rgb(20,24,200)'},            
        {from: 3, to: 9, color:{color:'red', highlight:'blue'}},        
        {from: 3, to: 10, color:{color:'red', highlight:'blue'}},        
        {from: 10, to: 4, color:{color:'red', highlight:'blue'}},        
        {from: 4, to: 5, color:{color:'red', highlight:'blue'}},
        {from: 4, to: 6, color:{color:'red', highlight:'blue'}},        
    ];    
    let data = {
        nodes: nodes,
        edges: edges
    };
    let options = {
        nodes: {
          shape: 'circle',
        },
        edges: {
          arrows: {
            to: { enabled: true, scaleFactor: 1, type: "arrow" }
          }
        },
        layout: {
          hierarchical: {
            direction: "LR",
            sortMethod: 'directed'
          }
        },
    };
    new vis.Network(el, data, options);
}
class PaginadorFlujo extends HTMLElement {
    constructor(){
        super();
        this.render();
    } 
  
    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `  
            <link rel="stylesheet" href="${config.w3css}">
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>          
            <div></div>
        `;
        const div = shadowRoot.querySelector('div');
        let app = new Vue({
            el: div,
            template: template,
            data() {
                return {                    
                    item: {
                        id: 1,
                        nombre: 'Todos'
                    }
                }
            },
            mounted() {
                this.init();
            },
            methods: {
                init(){
                    const grafica = this.$el.querySelector('#grafica');
                    addDiagrama(grafica);
                }                
            }
        });
    }
}

customElements.define("app-formulario-flujo", PaginadorFlujo); //Register the new element