export function templateHtml(titulo, cerrar, links, id) {
    let strLinks = '';
    for(let link of links) {
        strLinks += `<a href="${link.ruta}" class="w3-bar-item w3-button">${link.nombre}</a>\n`;
    }
return /*html*/`
<div class="bar">
    <div class="w3-bar w3-red">
        <span href="#" class="w3-bar-item w3-button w3-xlarge" propiedades>${titulo}</span>
        <button class="w3-bar-item w3-button w3-red w3-xlarge w3-right" onclick="onMenu${id}()">&#9776;</button>
    </div>
    <div class="w3-sidebar w3-bar-block w3-card w3-animate-fading" style="display:none;right:0;" id="right-menu" onclick="onMenu${id}()">
        <span class="w3-bar-item w3-button w3-large" id="btn-cerrar">${cerrar}</span>        
        ${strLinks}        
    </div>
</div>
`;
}

export function templateJs(id) {
    return `
    function onMenu${id}(){
        let display = document.getElementById("right-menu").style.display;
        display == 'contents' ? display = 'none' : display = 'contents';
        document.getElementById("right-menu").style.display = display;
    }    
    `;
}